
/* RESET */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #f2f2f2;
    font-family: 'Roboto', sans-serif;
    font-family: 'Poppins', sans-serif;
}
button {
    border: initial;
    background-color: initial;
    color: inherit;
    font: inherit;
    outline: none; /* Make sure there is a visual indicator for focus state */
}
a {
    text-decoration: none;
    color: #4C4E4D;
}
a:hover {
    text-decoration: underline;
}

select {
 
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  /* width: 100%;
  /* font-family: inherit; */
  width: auto;
  font-size: inherit;
  font-size: .9em;
  text-align: center;
  cursor: inherit;
  line-height: inherit;
}

/* UTILITIES */

/* Container */
.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
  }

  .link {
    color: #cc2d4b;
    cursor: pointer;
    font-weight: 500;
  }

  .errorAlert {
    color: #cc2d4b;
    font-weight: 500;
    font-size: 1.2em;
  }

  .form-group {
   display: flex;
   flex-direction: column;
  }

  .align-center {
      display: flex;
      justify-content: center;
    }

  .no-jobs {
    margin-bottom: 40em;
  }
  .not-found {
    margin-bottom: 40em;
  }

  @media (min-width: 768px) {
    .container {
      width: 750px;
    }
  }
  @media (min-width: 992px) {
    .container {
      width: 970px;
    }
  }
  @media (min-width: 1200px) {
    .container {
      width: 1200px;
    }
  }
  /* ---------------------------------------------------- */


  /* Margins */
  .mt {
      margin-top: 5em;
  }
  .mb {
    margin-bottom: 5em;
  }

  .mb-3 {
    margin-bottom: 3em;
  }
  .mb-2 {
    margin-bottom: 2em;
  }
  .mb-1 {
    margin-bottom: 1em;
  }
  .mt-1 {
    margin-top: 1em;
  }
 

/* Navbar */
.navbar {
    /* margin-top: 30px; */
    background-color: #fff;
    height: 55px;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
}

.navbar__logo {
  width: 130px;
  margin-top: 5px;
}

.navbar__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.navbar__ul {
    display: flex;
    list-style: none;
}


.navbar__link {
    font-family: 'poppins', 'Segoe UI', sans-serif, Tahoma, Geneva, Verdana;
    font-weight: 500;
    font-size: 1.1em;
    line-height: 1.5;
    margin-left: 30px;
}
.navbar__link:hover {
    text-decoration: underline;
}

.navbar__user {
  font-weight: 500;
}
.navbar__user-name {
  font-weight: 600;
  text-transform: capitalize;
}

  /* ---------------------------------------------------- */


  /* Search */

  .search__container {
    /* width: 80%; */
    display: flex;
    justify-content: center;
  }
  .search__card {
      /* max-width: 80%; */
      background-color: #FFF;
      border-radius: 12px;
      border: 1px solid #dedbdb;
      box-shadow: rgb(0 0 0 / 12%) 0px 6px 16px;
      padding: 40px;

  }

  .search__bar {
    border: none;
    box-shadow: none;
    background-color: #F2F2F2;
    border-radius: 7px;
    padding: 18px 20px;
    width: 93%;
    font-size: 1.1em;
    color: #333;
    font-weight: 500;
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
    transition: width 0.4s ease-in-out;
  }

  .search__bar:hover,
  .search__bar:focus {
    outline: none;
    width: 100%;

  }
 

  .search__slider {
    display: flex;
    justify-content: center;
  }

  .search__select-container {
    display: flex;
    column-gap: 30px;
  }
  /* .search__select {
    background-color: yellow;
  } */
  .search__button-container {
    display: flex;
    justify-content: center;
  }
  .search__button {
    cursor: pointer;
    padding: .7em 1.7em;
    background: linear-gradient(90deg,#cc2d4b,#d93c2e);
    border-radius: 12px;
    border: 1px solid #cc2d4b;
    color: #fff;
    font-size: 1.1em;
  }
  .search__button:hover {
    cursor: pointer;
    background: #FFF;
    color: #cc2d4b;
  }

  /* Jobs */
  .jobs__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  #display__jobs-mobile {
    display: none;
  }

  .display__jobs {
    display: block;
  }

  .jobs__list {
    width: 30%;
  }
  .jobs__detail {
    width: 60%;
  }

  .jobs__card {
    background-color: #FFF;
    border-radius: 12px;
    border: 1px solid #dedbdb;
    padding: 40px;
    cursor: pointer;
}
  .jobs__card:hover {
    border: 1px solid #cc2d4a7a;
    
}

.jobs__card-detail {
  position: sticky;
  top: 20px;
  /* background-color: #cc2d4b; */
  background: linear-gradient(90deg,#cc2d4b,#d93c2e);
  border-radius: 7px;
  border: 1px solid #dedbdb;
  padding: 40px;
  color: #FFF;

}

.jobs__title {
  font-weight: 500;
  text-transform: capitalize;
  font-size: 2.4em;
}
.jobs__card:hover .jobs__title {
  text-decoration: underline;
}


.jobs__title-detail {
  color: #FFF;
}
.jobs__detail-contact {
  display: flex;
  flex-direction: column;
  margin: .5em 0;
  font-weight: 600;
  font-size: 1.4em;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.jobs__detail-duration,
.jobs__detail-companyName,
.jobs__detail-startDate {
  font-size: 1.3em;
  font-weight: 400;
}


.jobs__detail-description {
  line-height: 1.5;
  color: #f2f2f2;
  font-size: 1.3em;
  text-align: justify;
  margin-top: .6em;
}

.jobs__salary {
  font-weight: 500;
  font-size: 1.5em;
}

.jobs__detail-salary {
  color: #FFF;
  font-size: 1.8em
}

.jobs__perHour {
  font-weight: 400;
  font-size: .6em;
}
.jobs__duration {
  color: #3e3d3d;
}
.jobs__company {
  font-weight: 500;
  text-transform: capitalize;
}
.jobs__details {
  font-weight: 400;
  text-transform: capitalize;
} 

.jobs__detail-address {
  margin: .5em 0;
}
/* Pagination */
.pagination__ul {
  display: flex;
  list-style: none;
}

.pagination__button {
  padding: 1em;
  color: rgb(34, 34, 34);
  font-weight: 500;
  background-color: #FFF;
  cursor: pointer;
  height: 2.5em;
  width: 2.5em;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  border: 1px solid #F2F2F2;
  transition: all 0.2s ease-in;

}

.pagination__button:hover,
.pagination__button-active {
  color: white;
  background-color: #CC2D4B;
}


/* Auth */

.login__container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: calc(100vh - 121.5px);
  overflow: hidden;
}
.register__container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 3em 0;
  overflow: hidden;
}
.form__container {
  display: flex;
  flex-direction: column;
  gap: 1.3em;
}
.login__card {
  width: 30%;
  max-width: 80%;
  background-color: #FFF;
  border-radius: 7px;
  -webkit-box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  padding: 40px;
}
.register__card {
  width: 60%;
  max-width: 80%;
  background-color: #FFF;
  border-radius: 7px;
  -webkit-box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  padding: 40px;
}
.auth__button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1em;
  margin-top: 1em;
  width: 93%;
}
.auth__button {
  cursor: pointer;
  display: block;
  padding: .5em 1em;
  background: linear-gradient(90deg,#cc2d4b,#d93c2e);
  border-radius: 12px;
  color: #fff;
  text-align: center;
  text-decoration: none;
  font-size: 1.1em;
  border: 1px solid #cc2d4b;
  transition: ease-in .2s;
  width: 100%;
}

.auth__button:nth-child(2) {
  background: #FFF;
  color: #cc2d4b;
}

.auth__button:hover {
  border: 1px solid #cc2d4b;
  background: #FFF;
  text-decoration: none;
  color: #cc2d4b;
}

.auth__button:nth-child(2):hover {
  background: linear-gradient(90deg,#cc2d4b,#d93c2e);
  color: #FFF;
}

.form-text {
  background-color: #F2F2F2;
  border-radius: 7px;
  padding: 18px 20px;
  width: 93%;
  font-size: 1.1em;
  color: #333;
  font-weight: 500;
  border: none;
  outline: none;
}
.form-text:focus,
.form-text:active,
.form-text2:focus,
.form-text2:active {
  border: none;
}

.form-text2 {
  background-color: #F2F2F2;
  border-radius: 7px;
  padding: 10px 12px;
  width: 100%;
  font-size: 1.1em;
  color: #333;
  font-weight: 400;
  border: none;
  outline: none;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.text-area {
  background-color: #F2F2F2;
  border-radius: 7px;
  padding: 10px 12px;
  width: 100%;
  font-size: 1.2em;
  color: #333;
  font-weight: 400;
  border: none;
  outline: none;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

/* User Jobs */
.userJobs__card {
  background-color: #FFF;
  border-radius: 7px;
  -webkit-box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  padding: 40px;
}
.userJobs__ul {
  list-style: none;
}
.userJobs__li {
  margin-top: 2em;
}

.addJob__card {
  background-color: #FFF;
  border-radius: 7px;
  -webkit-box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  padding: 40px;
}

.create__job {
  cursor: pointer;
  padding: .7em 1.8em;
  background: linear-gradient(90deg,#cc2d4b,#d93c2e);
  border-radius: 12px;
  border: 1px solid #cc2d4b;
  color: #fff;
  font-size: 1.1em;
  transition: ease-in .2s;


}
.create__job:hover {
  background: #FFF;
  color: #cc2d4b;
  text-decoration: none;

}

.userJobs__head {
  font-weight: 600;
}
.userJobs__jobDetails {
  margin: .6em 0;
  display: flex;
  flex-direction: column;
  gap: .1em;
}

.userJobs__editDelete-container {
  display: flex;
  gap: 1em;
}

.userJobs__btn {
  cursor: pointer;
  padding: .4em 1em;
  background: linear-gradient(90deg,#cc2d4b,#d93c2e);
  border-radius: 12px;
  border: 1px solid #cc2d4b;
  color: #fff;
  font-size: .9em;
  transition: ease-in .2s;
}

.userJobs__btn:hover {
  background: #FFF;
  color: #cc2d4b;
  text-decoration: none;

}

.userJobs__perHour {
  font-weight: 400;
  font-size: .6em;
}
.userJobs__hourlySalry {
  font-weight: 500;
}

/* Delete User */
.userJobs__container {
  height: calc(100vh - 202px);
}
.deleteUser__container {
  margin-bottom: 24em;
}
.userJobs__card-delete {
  max-width: 120%;
  background-color: #FFF;
  border-radius: 7px;
  -webkit-box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  box-shadow: 0 1px 3px rgb(0 0 0 / 13%);
  padding: 40px;
  margin-left: auto;
  margin-right: auto;

}

/* Footer */
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2em;
  background-color: #FFF;
  height: 55px;
  border-top: 1px solid #e5e5e5;
  margin-top: auto;
}

/* Tablet */
@media only screen and (max-width: 1000px) {
  #display__jobs-mobile {
    display: block;
  }

  #display__jobs {
    display: none;
  }
  .jobs__card {
    cursor:unset;
  }
  .jobs__card:hover {

    border: 1px solid #dedbdb;
  }
  .jobs__card:hover .jobs__title {
    text-decoration: none;
  }
.create__job {
display: block;
text-align: center;
}
}

@media only screen and (max-width: 770px) {
  .navbar__logo {
    width: 150px;
    margin-top: 5px;
}

.navbar__link {
  font-size: 1em;
}

.navbar__user {
  font-size: 1em;
  /* margin-top: .1em; */
}

.search__card {
  min-width: 600px;
}
.search__bar {
  width: 100%;
}

.search__select-container {
  flex-direction: column;
}
.navbar {
  height: 120px;
  padding: 10px 0;
}
.navbar__container {
  flex-direction: column;
  justify-content:space-between;
  /* padding: 50px 0;   */
}

}

@media only screen and (max-width: 632px) {

  .search__card {
    min-width: 400px;

  }
  #display__jobs-mobile {
    display: flex;
  
    flex-direction: column;
    align-items: center;
  }
  .jobs__card {
    max-width: 80%;

  }


}
/* Large Mobile or small Tablets */
@media only screen and (max-width: 500px) {

.navbar {
  height: 170px;
}
.navbar__ul {
  flex-direction: column;
  text-align: center;
}

.navbar__user {
  margin-bottom: .4em;
}
.jobs__card {
  max-width: 90%;
}
.search__card {
  min-width: 387px;
}

.form-text2 {
  font-size: 1em;
}
.text-area {
  font-size: 1em;
}

}


@media only screen and (max-width: 400px) {
  .search__card {
    min-width: 324px;
  }

  body {
    font-size: 94%;
  }
}

@media only screen and (max-width: 400px) {
  body {
    font-size: 90%;
  }
  .search__card {
    min-width: 306px;
  }
  .jobs__card {
    max-width: 100%;
  }
}
